import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CrossConsultationRequestComponent } from './components/cross-consultation-request/cross-consultation-request.component';
import { ReferralComponent } from './referral.component';

const routes: Routes = [
	{path:'',component: ReferralComponent},
	{path:'crossconsultationrequest', component: CrossConsultationRequestComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReferralRoutingModule { }
