import { Component, Input, OnInit } from '@angular/core';
import { Employee } from '../../../../master/unit-master/masters/Employee';
import { Department } from '../../../../master/department/masters/department';
import { Patient } from '../../../../patient/master/patient';
import { Observable } from 'rxjs';
import { FormBuilder, FormGroup } from '@angular/forms';
import { CrossConsultationRequests } from '../../masters/cross_consultation_request';
import { LookupEntity } from '../../../../master/lookup-module/masters/lookup-entity';
import { DepartmentService } from '../../../../master/department/services/department.service';
import { ActivatedRoute } from '@angular/router';
import { ConsultantService } from '../../../../encounter/services/consultant.service';
import { LookupService } from '../../../../master/lookup-module/services/lookup.service';
import { PatientService } from '../../../../patient/service/patient.service';
import { ReferralHospital } from '../../masters/referral_hospital';
import { PreferencesService } from '../../../../preferences/services/preference.service';
import { CrossConsultationRequestService } from '../../../../doctors-workbench/services/cross-consultation-request.service';
import { ReferralService } from '../../services/referral.service';
import { UnitService } from '../../services/unit.service';
import { ReferralHospitalService } from '../../services/referral-hospital.service';
import { Preferences } from '../../../../preferences/masters/preferences';


@Component({
  selector: 'app-cross-consultation-request',
  templateUrl: './cross-consultation-request.component.html',
  styleUrls: ['./cross-consultation-request.component.css']
})
export class CrossConsultationRequestComponent implements OnInit {
	department : Department = new Department();
	hospital :ReferralHospital = new ReferralHospital();
	departmentId : number;
	consultant : Employee = new Employee();
	@Input() patient : Observable<Patient> = new Observable<Patient>();
	crossConsultationForm = new FormGroup({})
	preference: Preferences = new Preferences();
	departmentList : Array<Department> =[];
	crossConsultationRequest: CrossConsultationRequests = new CrossConsultationRequests();
	hospitalList : Array<ReferralHospital> =[];
	consultantList : Array<Employee> =[];
	lookupEntityList : Array<LookupEntity> = new Array<LookupEntity>();
	priority : string ;
	search: string = ""
	crossConsultationRequestList: Array<CrossConsultationRequests> = [];
	
    
	
  constructor(private departmentService:DepartmentService,
		private referralService: ReferralService,
		private route:ActivatedRoute,
        private consultantService:ConsultantService,
		private preferenceService: PreferencesService,
		private unitService: UnitService,
		private hospitalService: ReferralHospitalService,
		private crossConsultationService: CrossConsultationRequestService,
		private lookupService:LookupService,
		private fb: FormBuilder,
		private patientService:PatientService
		) { }

	ngOnInit(): void {
		this.preference = JSON.parse(localStorage.getItem('preferences'))
		this.reloadData();
		this.reloadForm();
	}
	reloadForm(){
		this.crossConsultationForm = this.fb.group({
		   
			patient : null,
			priority : null,
			reqDate  : new(Date),
		    reqDepartment : null,
		    reqConsultant : null,
		     hospital : null,
             reason : null
		})
	}		
	reloadData()
	{
		this.patient = this.patientService.getPatient();
		
		this.lookupService.getLookupEntityListByCategoryId(293).subscribe( 
			data => {console.log("Entity List",data)
			this.lookupEntityList = data},
			err => console.log(err)
		)
		this.crossConsultationService.getCrossConsultationRequestsList().subscribe(
			data => {this.crossConsultationRequestList = data, console.log("This is crossConsultation Data",data)},
			err => console.log(err)
		)
		this.hospitalService.getReferralHospitalList().subscribe(data =>{
			this.hospitalList = data;
			console.log("This is hospital List",this.hospitalList)
		})
		this.departmentService.getDepartmentList().subscribe(data =>{
			this.departmentList = data;
		});
		
		this.preferenceService.getPreferenceByUserId(1)
		.subscribe(
			data =>{
				this.preference  = data;
				console.log(this.preference, "This is preference")
			},
			err => {
				console.log(err)
			}
		)
	}


showConsultant(event){
	this.departmentId = event.departmentId;	
	/* for(let department of this.departmentList){
		console.log(this.departmentList)
		if(department.departmentId == this.departmentId){
			this.department = department;
		}
	}
	console.log(this.department.departmentId)
	console.log(this.consultantList)*/
	this.unitService.getEmployeeListFromUnitByDepartmentId(this.departmentId).subscribe(data =>{
			this.consultantList = data;
		});

}

deleteCrossConsultationRequest(service) {
		console.log(service)
		let index = this.crossConsultationRequestList.indexOf(service)
		this.crossConsultationRequestList.splice(index,index+1)
		}
	
showDepartment(hospital){
	this.departmentService.getDepartmentsByBranchId(hospital.branch.id).subscribe(
		data =>{
			this.departmentList = data;
		},
		err => console.log(err)
		)
}

	saveCrossConsultationRequest(){
		let crossConsultationRequest = new CrossConsultationRequests();
	    
		
		/// To be changed Employeeee
        this.lookupService.getLookupEntityById(323).subscribe( 
		data => {
			crossConsultationRequest = this.crossConsultationForm.value;
		let patient:any = this.patient;
		crossConsultationRequest.patient = patient;
		crossConsultationRequest.site = null;
		crossConsultationRequest.visit = null;
		crossConsultationRequest.approvalStatus =data;
		crossConsultationRequest.department = this.preference.department;
		crossConsultationRequest.consultant = this.preference.user.employee;
		console.log("This is before saving ",crossConsultationRequest)
		this.crossConsultationService.createCrossConsultationRequest(crossConsultationRequest).subscribe(
			data => {
				 
				this.reloadData();
				console.log("After Saving",data)
			},
			err => console.log(err)
		)
			 
		}
)

		
	}
 
	searchCrossConsultaionRequest(){
		console.log('This is called')
		console.log(this.search);
		console.log("This is updated Patient",this.patient)
		this.crossConsultationService.searchCrossConsultation(this.search).subscribe( 
			data => {
				this.crossConsultationRequestList = data;
			},
			err => console.log(err)
			
		)
	}

}


